body .current {
  color: #b3ff00;
}

html {
  background-color: #d08a8a;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.burger-text {
  position: absolute;
  font-size: 30px;
  text-align: center;
  top: 2px;
  left: 14px;
}

nav a:hover {
  background-color: gray;
}

nav {
  background-color: #c69c62;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  opacity: 1;
}

.nav-link {
  color: black;
  text-decoration: none;
  float: left;
  display: block;
  width: 180px;
  height: 25px;
  border-width: 3px 3px 3px 0px;
  border-style: solid; 
  border-color: black;
  text-align: center;
  padding: 10px 0;
  opacity: 1;
  font-size: 22px;
  font-weight: bold;
}

.hamburger {
  color: black;
  float: left;
  display: block;
  width: 50px;
  height: 25px;
  border: 3px solid black;
  padding: 10px 0;
  opacity: 1;
}

p {
  font-size: 25px;
  width: 800px;
}

h1 {
  font-size: 45px;
}

.background {
  font-size: 20px;
  position: fixed; 
  width: 640px;
  //white-space: pre;
  color: #ad7222e0;
  z-index: -1;
  left: 50px;
  transform: rotate(20deg);
  margin-top: 100px;
}

body {
  position: relative;
  margin-top: 50px;
  margin-left: 10px;
  height: 1500px;
}

@media only screen and (max-width:1200px) {
  .nav-link {
    width: 15%
  }
}

@media only screen and (max-width:760px) {
  .nav-link {
    width: 12%
  }
}

@media only screen and (max-width:731px) {
  .nav-link {
    display: none;
  }
}
